/* ===================================
   WORKS - FEATURED PROJECTS
   =================================== */

.works-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--spacing-5xl) 0;
  background-color: var(--color-bg-primary);
  overflow-x: hidden;
  width: 100%;
}

.works-container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--spacing-2xl);
  overflow-x: hidden;
}

/* === TITLE === */
.works-title {
  font-size: clamp(2rem, 5vw, var(--font-size-4xl));
  color: var(--color-text-bright);
  margin-bottom: var(--spacing-4xl);
  position: relative;
  display: inline-block;
  width: fit-content;
}

.works-title::before {
  content: '04. ';
  font-family: var(--font-mono);
  font-size: var(--font-size-xl);
  color: var(--color-primary);
  margin-right: var(--spacing-sm);
  font-weight: var(--font-weight-normal);
}

/* Línea decorativa después del título */
.works-title::after {
  content: '';
  position: absolute;
  top: 50%;
  left: calc(100% + var(--spacing-lg));
  width: 300px;
  height: 1px;
  background-color: var(--color-text-muted);
  opacity: 0.3;
}

/* === FEATURED PROJECT LAYOUT === */
.featured-project {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 0;
  align-items: start;
  margin-bottom: 90px;
  position: relative;
}

/* === PROJECT CONTENT === */
.project-content {
  grid-column: 7 / -1;
  grid-row: 1;
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  text-align: right;
}

.project-label {
  margin-bottom: var(--spacing-sm);
  text-align: right;
}

.project-featured {
  font-family: var(--font-mono);
  font-size: var(--font-size-sm);
  color: var(--color-primary);
  font-weight: var(--font-weight-normal);
}

.project-title {
  font-size: clamp(1.5rem, 4vw, var(--font-size-3xl));
  color: var(--color-text-bright);
  margin-bottom: var(--spacing-md);
  font-weight: var(--font-weight-bold);
  line-height: 1.1;
  text-align: right;
}

.project-description {
  background-color: rgba(23, 42, 69, 0.9);
  padding: var(--spacing-lg) var(--spacing-xl);
  margin-bottom: var(--spacing-lg);
  margin-top: 0;
  box-shadow: 0 10px 30px -15px rgba(0, 0, 0, 0.7);
  position: relative;
  z-index: 3;
  border-radius: var(--border-radius-md);
  backdrop-filter: blur(10px);
  width: 120%;
  max-width: none;
}

/* Descripción cuando el texto está a la DERECHA (proyectos impares) */
.project-description-right {
  margin-left: auto;
  margin-right: -20%;
}

/* Descripción cuando el texto está a la IZQUIERDA (proyectos pares) */
.project-description-left {
  margin-left: -20%;
  margin-right: auto;
}

.project-description p {
  color: var(--color-text-secondary);
  font-size: var(--font-size-base);
  line-height: 1.7;
  margin: 0;
}

.project-description strong {
  color: var(--color-primary);
  font-weight: var(--font-weight-medium);
}

/* === TECH STACK === */
.project-tech-list {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-md);
  list-style: none;
  padding: 0;
  margin: 0 0 var(--spacing-lg) 0;
  font-family: var(--font-mono);
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  justify-content: flex-end;
}

.project-tech-list li {
  position: relative;
}

/* === PROJECT LINKS === */
.project-links {
  display: flex;
  gap: var(--spacing-md);
  justify-content: flex-end;
}

.project-link {
  color: var(--color-text-bright);
  font-size: var(--font-size-xl);
  transition: all var(--transition-base);
  padding: var(--spacing-xs);
}

.project-link:hover {
  color: var(--color-primary);
  transform: translateY(-3px);
}

/* === PROJECT IMAGE === */
.project-image {
  grid-column: 1 / 7;
  grid-row: 1;
  position: relative;
  z-index: 1;
}

/* === LAYOUT ALTERNADO - Proyectos pares invertidos === */
.featured-project:nth-child(even) .project-content {
  grid-column: 1 / 7;
  text-align: left;
}

.featured-project:nth-child(even) .project-label {
  text-align: left;
}

.featured-project:nth-child(even) .project-title {
  text-align: left;
}

.featured-project:nth-child(even) .project-tech-list {
  justify-content: flex-start;
}

.featured-project:nth-child(even) .project-links {
  justify-content: flex-start;
}

.featured-project:nth-child(even) .project-image {
  grid-column: 7 / -1;
}

.image-wrapper {
  position: relative;
  border-radius: var(--border-radius-lg);
  overflow: hidden;
  box-shadow: 0 20px 50px -15px rgba(0, 0, 0, 0.6);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background-color: #0a192f;
}

.image-wrapper::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-primary);
  mix-blend-mode: multiply;
  opacity: 0.25;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 2;
}

.image-wrapper:hover::before {
  opacity: 0;
  background-color: transparent;
}

.image-wrapper:hover {
  transform: translateY(-8px);
  box-shadow: 0 30px 60px -15px rgba(0, 0, 0, 0.8);
}

.image-wrapper img {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  aspect-ratio: 16 / 9;
  filter: grayscale(100%) contrast(1) brightness(80%);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  mix-blend-mode: multiply;
}

.image-wrapper:hover img {
  filter: grayscale(0%) contrast(1) brightness(100%);
  mix-blend-mode: normal;
}

/* === MORE PROJECTS BUTTON === */
.more-projects {
  display: flex;
  justify-content: center;
  margin-top: var(--spacing-5xl);
  padding-bottom: var(--spacing-3xl);
}

.more-projects-button {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-md);
  padding: var(--spacing-md) var(--spacing-xl);
  background-color: transparent;
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
  font-family: var(--font-mono);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
  border-radius: var(--border-radius-md);
  cursor: pointer;
  transition: all var(--transition-base);
  text-decoration: none;
}

.more-projects-button:hover {
  background-color: rgba(100, 255, 218, 0.1);
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(100, 255, 218, 0.2);
}

.more-projects-button i {
  font-size: var(--font-size-xl);
  transition: transform var(--transition-base);
}

.more-projects-button:hover i {
  transform: scale(1.1);
}

/* === NAVIGATION === */
.works-navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-2xl);
  margin-top: var(--spacing-3xl);
}

.nav-arrow {
  background-color: transparent;
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
  width: 48px;
  height: 48px;
  border-radius: var(--border-radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: var(--font-size-xl);
  transition: all var(--transition-base);
  user-select: none;
}

.nav-arrow:hover {
  background-color: rgba(100, 255, 218, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(100, 255, 218, 0.2);
}

.nav-arrow:active {
  transform: translateY(0);
}

.project-counter {
  font-family: var(--font-mono);
  font-size: var(--font-size-lg);
  color: var(--color-text-secondary);
  user-select: none;
}

.project-counter .current {
  color: var(--color-primary);
  font-weight: var(--font-weight-bold);
}

.project-counter .separator {
  margin: 0 var(--spacing-xs);
  color: var(--color-text-dim);
}

