/* ===================================
   ABOUT SECTION - ESTILO MINIMALISTA
   =================================== */

.about-section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: var(--spacing-5xl) var(--spacing-2xl);
  gap: var(--spacing-5xl);
  width: 100%;
  overflow-x: hidden;
}

/* === CONTENIDO PRINCIPAL === */
.about-content {
  display: grid;
  grid-template-columns: 3fr 2fr;
  gap: var(--spacing-4xl);
  align-items: center;
}

/* === TEXTO === */
.about-text {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
}

.about-text h2 {
  font-size: clamp(2rem, 5vw, var(--font-size-4xl));
  color: var(--color-text-bright);
  margin-bottom: var(--spacing-md);
  position: relative;
  display: inline-block;
  width: fit-content;
}

/* Número de sección estilo Brittany */
.about-text h2::before {
  content: '02. ';
  font-family: var(--font-mono);
  font-size: var(--font-size-xl);
  color: var(--color-primary);
  margin-right: var(--spacing-sm);
  font-weight: var(--font-weight-normal);
}

/* Línea decorativa después del título */
.about-text h2::after {
  content: '';
  position: absolute;
  top: 50%;
  left: calc(100% + var(--spacing-lg));
  width: 300px;
  height: 1px;
  background-color: var(--color-text-muted);
  opacity: 0.3;
}

.about-text p {
  font-size: var(--font-size-lg);
  line-height: var(--line-height-relaxed);
  color: var(--color-text-secondary);
  margin: 0;
}

.about-text strong {
  color: var(--color-primary);
  font-weight: var(--font-weight-medium);
}

/* === IMAGEN === */
.about-image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-image img {
  width: 100%;
  max-width: 350px;
  height: auto;
  border-radius: var(--border-radius-lg);
  filter: grayscale(30%);
  transition: all var(--transition-base);
  position: relative;
  z-index: 2;
}

/* Efecto de overlay verde en hover */
.about-image::before {
  content: '';
  position: absolute;
  top: 20px;
  left: 20px;
  width: 100%;
  max-width: 350px;
  height: 100%;
  border: 2px solid var(--color-primary);
  border-radius: var(--border-radius-lg);
  z-index: 1;
  transition: all var(--transition-base);
}

.about-image:hover img {
  filter: grayscale(0%);
  transform: translate(-10px, -10px);
}

.about-image:hover::before {
  transform: translate(10px, 10px);
}

/* === SEGUNDA PARTE (Download CV + Experience) === */
.about-parteDos {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-3xl);
  margin-top: var(--spacing-3xl);
  width: 100%;
}

.container-download {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.experience {
  width: 100%;
}

